<template>
  <v-menu
    bottom
    left
    offset-y
    origin="top right"
    transition="scale-transition"
  >
    <template v-slot:activator="{ attrs, on }">
      <v-btn
        class="ml-2"
        min-width="0"
        text
        v-bind="attrs"
        v-on="on"
      >
        <v-badge
          v-if="notifications.length !== 0"
          bordered
          color="red"
          overlap
        >
          <template v-slot:badge>
            <span>{{ notifications.length }}</span>
          </template>
          <v-icon>mdi-bell</v-icon>
        </v-badge>
        <v-icon v-else>
          mdi-bell
        </v-icon>
      </v-btn>
    </template>

    <v-list
      v-if="notifications.length !== 0"
      flat
      nav
    >
      <app-bar-item
        v-for="(n, i) in notifications"
        :key="i"
        link
      >
        <v-list-item-content>
          <v-list-item-title>{{ n }}</v-list-item-title>
        </v-list-item-content>
      </app-bar-item>
    </v-list>
    <v-list
      v-else
      disabled
    >
      <app-bar-item>Não há novas notificações</app-bar-item>
    </v-list>
  </v-menu>
</template>

<script>
  import AppBarItem from '@/components/app/BarItem'

  export default {
    name: 'DefaultNotifications',

    components: { AppBarItem },

    data: () => ({
      notifications: [],
    }),

    created () {
      this.fetchNotifications()
    },

    methods: {
      fetchNotifications () {
        return fetch('https://sheetdb.io/api/v1/1q4dfgpu4na0b')
          .then((res) => res.json())
          .then((res) => {
            let notif = res.filter(r => r.STATUS === 'PRE-PEDIDO')
            notif.forEach(solic => this.notifications.push(solic.ID + ' - Nova solicitação de Licença'))
            console.log(this.notifications)
          }
        )
      },
    },
  }
</script>
